@import "../../styles/scss.scss";
@import "../../styles/colors.scss";

.page {
  @include flexProperties(row, flex-start, flex-start, $fullWidth: true);
  gap: 0px;

  /* Chrome, Safari, Edge, Opera */
  input::-webkit-outer-spin-button,
  input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }

  /* Firefox */
  input[type="number"] {
    -moz-appearance: textfield;
  }

  &__fieldsContainer {
    background: #2f3443;
    border-radius: 8px;

    &__image_container {
      display: flex;
      flex: 1;
      align-items: center;
      gap: 40px;

      &__avatar_container {
        position: relative;

        &__icon {
          position: absolute;
          top: 55px;
          left: 55px;
        }
      }
    }
    &__form {
      display: flex;
      flex-direction: column;
      flex: 1;
      margin-top: 20px;
      gap: 40px;
      padding-bottom: 40px;

      &__formfield {
        display: flex;
        flex-direction: column;
        gap: 10px;

        &__label {
          @include flexProperties(row, flex-start, center, $fullWidth: true);
          gap: 10px;
          color: white;
          margin-left: 10px;
          font-size: 14px;
          &__image {
            height: 18px;
            width: 18px;
          }
        }

        &__input {
          color: #fff !important;
          border: 1px solid #4ffcb7 !important;
          background: #222c41;
          outline: none;
          padding: 15px;
          font-size: 14px;
          border-radius: 8px;
          width: 100%;

          option {
            background-color: #222c41;
          }

          &::placeholder {
            color: #ffffff9d;
          }
        }

        &__select {
          border: 1px solid #4ffcb7 !important;
          background: #192233;
          padding: 0px;
          border-radius: 12px !important;
          color: #fff !important;
          outline-color: transparent;
          font-size: 14px !important;

          option {
            padding: 0;
            font-size: 14px !important;
          }
        }

        &__button {
          @include activeThemeBotton();
          padding: 6px 30px !important;
          // background-color: #4FFCB7 !important;
          // //    width: fit-content;
          // width: 100px;
          // text-align: center;
          // margin: 0 auto !important;
          // display: block;
          // &:hover {
          //     background-color: #4FFCB7 !important;
          // }
        }

        &__phoneinput {
          border: 1px solid #4ffcb7 !important;
          border-radius: 8px;
          padding: 15px;
          color: #979797 !important;

          input {
            color: #979797 !important;
            background: transparent;
            outline: none;
            font-size: 18px;
            border: none;
          }

          option {
            background-color: #222c41;
            color: #c2c2c2;
          }
        }

        &__remove {
          color: #4ffcb7;
          cursor: pointer;
          margin-top: 12px;
          transform: translateX(-50px);
        }
      }

      &__action {
        gap: 60px;
        @include flexProperties(row, flex-end, center, $fullWidth: true);
        width: 100%;

        &__cancel_btn {
          @include activeThemeBotton(16px, 10px);
          @include fontSize();
          height: 40px;
          padding: 5px 10px !important;
        }

        &__submit_btn {
          @include activeThemeBotton(16px, 10px);
          @include fontSize();
          height: 40px;
          padding: 5px 10px !important;
        }
      }

      &__list {
        background-color: #192233 !important;
        color: #fff !important;
        // border-bottom: 1px solid #fff;
        p {
          color: #fff !important;
        }
      }
