@import "../../../styles/colors.scss";
@import "../../../styles/scss.scss";

.page {
    @include flexProperties(column, flex-start, flex-start, $fullWidth: true);
    gap: 20px;

    &__statusBar {
        @include flexProperties(row, space-between, flex-start, $fullWidth: false);
        width: calc(100% - 250px);

        &__heading {
            font-weight: bold;
            color: #4ffcb7;
        }

        &__icon {
            cursor: pointer;
            width: 25px;
        }
    }

    &__banner {
        background: $base-container-backgroung-color;
        width: calc(100%);
        height: 370px;
        max-height: 370px;
        border-radius: 12px !important;

        &__views {
            font-weight: bold;
            font-size: 2rem;
            @media screen and (min-width: 1201px) and (max-width: 1520px) {
                font-size: 1.3rem;
            }
        }

        &__conatiner {
            @include flexProperties(column, center, center, $fullWidth: true);
            gap: 20px;
            width: 100%;
            &__top_heading {
                @include customHeading(20px);
            }
            &__innercontainer {
                width: 100%;
                &__bar_head_container {
                    margin-bottom: 20px !important;

                    &__head {
                        @include flexProperties(row, space-between, center, $fullWidth: true);
                        margin-bottom: 10px !important;
                        &__icon_title {
                            @include flexProperties(row, flex-start, center, $fullWidth: false);
                        }
                    }
                }
            }
            &__icon {
                height: 23px !important;
                width: 22px !important;
            }
            &__label {
                margin-left: 10px;
                @include customParagraph(16px);
            }
        }
    }

    &__main_content {
        width: calc(100%);
    }
}

@media only screen and (max-width: 1425px) {
    .page {
        &__statusBar {
            width: 100%;
        }

        &__banner {
            width: 100%;
        }

        &__main_content {
            width: 100%;
        }
    }
}

@media only screen and (max-width: 1267px) {
    .page {
        &__banner {
            &__conatiner {
                &__content {
                    &__details {
                        &__box {
                            &__title {
                                font-size: 18px;
                            }

                            &__value {
                                font-size: 16px;
                            }
                        }
                    }
                }
            }
        }
    }
}

